<template>
  <div class="unplaced">
      <div class="unplacedBox" v-if="!unplacedFlag">
        <div class="unplacedIconBox">
          <i class="icon iconfont unplacedIcon" @click="showActive()">&#xe6b9;</i>
        </div>
        <div class="unplacedNumBox">
         <label style="color:gray" id="label1">{{$t('lang.unplaced')}}</label>
          <p class="initDisplay" >
            <span class="wineNum" style="color:gray">{{totalNum_unplaced}}</span>
            <span class="iconfont wineBottle" style="color:gray">&#xe6c1;</span>
            <i v-if="fromAddPositionFlag==1 && delNum > 0 " class="delNum">-{{delNum}}</i>
            <!-- <span class="removeNum"></span> -->
          </p>
        </div>
      </div>
      <div class="unplacedBox" v-if="unplacedFlag">
        <div class="unplacedIconBox" style="background:black">
          <i class="icon iconfont unplacedIcon" style="color:#fff;">&#xe6b9;</i>
        </div>
        <div class="unplacedNumBox">
         <label id="label2">{{$t('lang.unplaced')}}</label>
          <p class="wineCountP">
            <i
              class="substractIcon icon iconfont"
              :style="{color:totalNum_unplaced==0?'#ccc':'black'}"
              @click="substractUnplacedNum()"
            >
              &#xe707;
            </i>
            <span class="wineNum">{{totalNum_unplaced}}</span>
            <i class="iconfont wineBottle">&#xe6c1;</i>
            <i
              class="icon iconfont addIcon"
              :style="{color:totalNum_unplaced==200?'#ccc':'black'}"
              @click="addUnplacdNum()"
            >&#xe695;
            </i>
          </p>
        </div>
      </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data(){
    return {
      pageName:"unplaced"
    }
  },
  props:{
    unplacedFlag:{
      type:Boolean,
      default:false
    },
    totalNum_unplaced:{
      type:Number,
      default:0
    },
    fromAddPositionFlag:{
      default:0,
    },
    delNum:{
      default:0,
    },
  },
  watch:{
    delNum(v1new,v2old){
      console.log("v1new===========",v1new)
      console.log("v2old===========",v2old)
    }
  },
  created(){

  },
  mounted(){
     console.log("this.fromAddPositionFlag==============",this.fromAddPositionFlag);
     let height = document.getElementById('label1').offsetHeight
     document.getElementsByClassName('unplacedNumBox')[0].style.paddingTop = 0.5 * (60-30) +'px'

  },
  computed: {

  },
  methods: {
    showActive(){
      this.$emit("showActive")
    },
     addUnplacdNum(){
       this.$emit("addUnplacdNum");
    },
    substractUnplacedNum(){
      this.$emit("substractUnplacedNum")
    }
  }
}
</script>
<style lang="less">
.unplacedBox{
      width: 100%;
      margin: 20px 0;
      display: flex;
      justify-content: space-between;
      .unplacedIconBox{
        height: 60px;
        line-height: 50px;
        text-align: center;
        border:1px solid black;
        width: 42px;
        border-radius:5px;
        font-size: 30px;
      }
      .unplacedNumBox{
        width: 80%;
        display: flex;
        justify-content: space-between;
        height: 60px;
        /*line-height: 30px;*/
          label{
            font-size: 16px;
            font-weight: bold;
          }
      }
      .initDisplay{
        margin-right: 25px;
        position: relative;
        width: 18%;
        .wineNum{
          font-size: 20px;
          font-weight: bold;
        }
        .wineBottle{
         font-size: 20px;
        }
        .delNum{
          position: absolute;
          left:32px;
          top:-10px;
          font-size: 16px;
          color:red;
          font-weight: bold;
        }
      }

    }
    .wineCountP{
      // padding-right: 4px;
      .substractIcon{
        padding-right: 10px;
      }
      .wineNum{
        font-size: 20px;
        font-weight: bold;
      }
      .wineBottle{
        font-size: 20px;

      }
      .addIcon{
         font-size: 20px;
        //  padding-left: 10px;
      }
    }
</style>







